                <van-cell title="通知提醒" is-link :to="util.getUrl({path: '/pages/plugin/agent/config/notify'})"/>
                <van-cell title="配送设置" is-link :to="util.getUrl({path: '/pages/plugin/agent/config/delivery'})"/>
                <van-cell title="订单设置" is-link :to="util.getUrl({path: '/pages/plugin/agent/config/order'})"/>
                <van-cell title="时限要求" is-link :to="util.getUrl({path: '/pages/plugin/agent/config/timelimit'})"/>
            </van-cell-group>
        </div>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader
		},
		methods: {

		}
	}
</script>

<style>
    #agentconfig-index .content .tip{
        padding: 10px 15px;
        font-size: 14px;
        color: #7a7a7a;
    }
</style>


